.button {
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgb(51, 51, 0.5);
  cursor: pointer;
  margin-right: 1rem;
}
.button:hover {
  color: black;
  background-color: #e6e6e6;
}
.descipition {
  width: 45rem;
